.checkout {
  &-form {
    padding: 30px 30px 10px 30px;
    border: 1px solid $border;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 24px;
  }

  .ui-filed-name {
    font-size: 16px;
    font-size: 1rem;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 10px;
    display: block;
  }

  .subtotal {
    position: relative;
  }

  .subtotal::before {
    content: "";
    position: absolute;
    top: 101px;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #f5f6f7;
  }

  .payment-method {
    background-color: #fff;
    border: 1px solid #e1e3e5;
    border-radius: 10px;
    padding: 30px 30px 0;
    margin-bottom: 24px;
  }

  .payment-method-title,
  .shopping-cart-name {
    font-size: 32px;
    font-size: 2rem;
    font-weight: 600;
    color: #484949;
  }

  .payment-method-header,
  .shopping-card-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 22px;
    -webkit-box-pack: justify;
    align-items: center;
    -webkit-align-items: center;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }

  .payment-method-edit .icon {
    width: 22px;
    height: 22px;
    fill: none;
    stroke: #484949;
    stroke-width: 1.5px;
  }

  .payment-items {
    margin-bottom: 30px;

    .payment-item {
      display: flex;
      align-items: center;
      gap: 10px;
      cursor: pointer;

      font-size: 16px;
      font-size: 1rem;
      font-weight: 600;
      line-height: 24px;
      color: #484949;
      cursor: pointer;
    }

    .payment-item-input {
      width: 20px;
      height: 20px;
      accent-color: #484949;
      cursor: pointer;
    }

    .payment-item-pic {
      width: 16px;
      height: 20px;
    }
  }

  .shopping-card-edit .icon {
    width: 22px;
    height: 22px;
    fill: none;
    stroke: #484949;
    stroke-width: 1.5px;
  }

  @media (max-width: 395px) {
    .shopping-card-edit {
      display: none;
    }

    .subtotal-head {
      margin-bottom: 30px;
    }

    .subtotal::before {
      top: 23%;
    }
  }

  @media (max-width: 370px) {
    .shopping-cart-name span {
      display: flex;
      flex-wrap: wrap;
    }
    .payment-method-header {
      gap: 20px;
    }
  }
  @media (max-width: 599px) {
    .payment-method {
      padding: 16px 16px 0px;
    }
    .checkout-form {
      padding: 16px 16px 4px;
    }

    .subtotal::before {
      top: 24%;
    }
  }
}
